@import url(https://fonts.googleapis.com/css?family=Actor:regular,bold,italic&subset=latin,latin-ext);
@import url(https://fonts.googleapis.com/css2?family=Oxyge&family=Red+Rose);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --clr-neutral-100: #fff;
  --clr-primary-200: #fd5c6a;
  --clr-primary-400: #9065fe;

  --ff-primary: "Red Rose", serif;
  --ff-accent: "Raleway", serif;
}

button,
a,
input {
  font-family: "Raleway", serif;
  font-family: var(--ff-accent);
}

html,
body,
h1,
h2,
p {
  margin: 0;
}

h1,
h2 {
  margin-bottom: 1rem;
}

h1 {
  font-size: 3.75rem;
}

h2 {
  font-size: 1.125rem;
  font-weight: normal;
}

@media (max-width: 540px) {
  h1 {
    font-size: 1.875rem;
  }

  h2 {
    font-size: 1rem;
  }
}

p {
  font-size: 1rem;
  padding: 0.625rem;
  font-weight: 400;
}

button {
  border-radius: 1.5rem;
  background: none;
  border: 1px solid white;
  color: white;
  font-size: 1rem;
}

@-webkit-keyframes change-color {
  0% {
    background: #fd5c6a;
    background: var(--clr-primary-200);
  }
  100% {
    background: #9065fe;
    background: var(--clr-primary-400);
  }
}

@keyframes change-color {
  0% {
    background: #fd5c6a;
    background: var(--clr-primary-200);
  }
  100% {
    background: #9065fe;
    background: var(--clr-primary-400);
  }
}

html {
  scroll-behavior: smooth;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

::-webkit-input-placeholder {
  font-style: italic;
  color: white;
}
:-moz-placeholder {
  font-style: italic;
  color: white;
}
::-moz-placeholder {
  font-style: italic;
  color: white;
}
:-ms-input-placeholder {
  font-style: italic;
  color: white;
}

input,
label {
  display: block;
}

.error {
  color: yellow;
}

textarea:focus,
input:focus,
textarea,
input {
  outline: none;
  background: none;
  border: none;
  border-bottom: 1px solid var(--clr-neutral-100);
  margin-bottom: 0.25rem;
  color: var(--clr-neutral-100);
  padding-bottom: 5px;
}

label {
  font-weight: 400;
}

.text-input {
  width: 97%;
}

.error {
  color: #ff5f5f;
  font-weight: 400;
  font-size: 14px;
}

.text-input-container {
  margin: 0.5rem 0;
  min-height: 65px;
}

.small-input-container {
  margin: 0.5rem 0;
  min-height: 45px;
}

.platform-header-label {
  margin-bottom: 0.5rem;
}

.platform-radio-group {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.platform-radio-group > * + * {
  margin-left: 1rem;
}

.radio {
  background-color: #d8d6ff;
  top: 0;
  left: 0;
}

@media (min-width: 700px) {
  .form {
    width: 400px;
  }
}

@media (min-width: 1200px) {
  .text-input-container {
    margin: 1rem 0;
  }
  .small-input-container {
    margin: 1rem 0;
  }
  .platform-header-label {
    margin-bottom: 1rem;
  }
  .platform-radio-group {
    margin-bottom: 1rem;
  }
}

