::-webkit-input-placeholder {
  font-style: italic;
  color: white;
}
:-moz-placeholder {
  font-style: italic;
  color: white;
}
::-moz-placeholder {
  font-style: italic;
  color: white;
}
:-ms-input-placeholder {
  font-style: italic;
  color: white;
}

input,
label {
  display: block;
}

.error {
  color: yellow;
}

textarea:focus,
input:focus,
textarea,
input {
  outline: none;
  background: none;
  border: none;
  border-bottom: 1px solid var(--clr-neutral-100);
  margin-bottom: 0.25rem;
  color: var(--clr-neutral-100);
  padding-bottom: 5px;
}

label {
  font-weight: 400;
}

.text-input {
  width: 97%;
}

.error {
  color: #ff5f5f;
  font-weight: 400;
  font-size: 14px;
}

.text-input-container {
  margin: 0.5rem 0;
  min-height: 65px;
}

.small-input-container {
  margin: 0.5rem 0;
  min-height: 45px;
}

.platform-header-label {
  margin-bottom: 0.5rem;
}

.platform-radio-group {
  display: flex;
  flex-direction: row;
}

.platform-radio-group > * + * {
  margin-left: 1rem;
}

.radio {
  background-color: #d8d6ff;
  top: 0;
  left: 0;
}

@media (min-width: 700px) {
  .form {
    width: 400px;
  }
}

@media (min-width: 1200px) {
  .text-input-container {
    margin: 1rem 0;
  }
  .small-input-container {
    margin: 1rem 0;
  }
  .platform-header-label {
    margin-bottom: 1rem;
  }
  .platform-radio-group {
    margin-bottom: 1rem;
  }
}
